<script lang="ts">
  import { onMount } from "svelte";
  import ChatView from "./lib/components/ChatView.svelte";
  import { Trash2, Maximize, Minimize } from "lucide-svelte";
  import { X } from "lucide-svelte";
  import { InitService } from "./lib/services/init";

  const { settings } = $props();
  const initService = new InitService(settings);
  let isMaximized = $state<boolean>(false);

  onMount(async () => {
    await initService.initialize();
  });

  const resetChat = () => {
    initService.resetChat();
  };

  const toggleMaximize = () => {
    if (isMaximized) {
      const event = new CustomEvent("minimize-widget");
      document.dispatchEvent(event);
      isMaximized = false;
    } else {
      const event = new CustomEvent("maximize-widget");
      document.dispatchEvent(event);
      isMaximized = true;
    }
  };
</script>

<main>
  <!-- Header -->
  <header class="main-header" style="background-color: {settings.color_hex};">
    <div class="brand">
      {#if settings.header_logo}
        <img
          src={settings.header_logo}
          alt={settings.title_text ? settings.title_text + " logo" : "Logo"}
          class="logo"
          loading="lazy"
          decoding="async"
        />
      {/if}
      <div class="title" title={settings.title_text}>{settings.title_text}</div>
    </div>
    <div class="button-group">
      <!-- Close button visible on full-screen/mobile -->
      <button
        class="icon-button close-mobile"
        aria-label="Close chat"
        onclick={() => document.dispatchEvent(new CustomEvent("close-widget"))}
      >
        <X size={20} />
      </button>
      <button
        class="icon-button"
        aria-label={isMaximized ? "Collapse chat" : "Expand chat"}
        onclick={toggleMaximize}
      >
        {#if isMaximized}
          <Minimize size={20} />
        {:else}
          <Maximize size={20} />
        {/if}
      </button>
      <button
        class="icon-button reset-mobile"
        aria-label="Reset chat"
        onclick={resetChat}
      >
        <Trash2 size={20} />
      </button>
    </div>
  </header>

  <div class="content">
    <ChatView {settings} />
  </div>
</main>

<style>
  main {
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #f7f7f7; /* gray-50 */
    overflow: hidden; /* preserve container rounded-corner clipping */
  }

  header {
    color: white;
    padding: 0.5rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    flex-shrink: 0;
  }

  .main-header {
    color: white;
    padding: 0.5rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    flex-shrink: 0;
    position: relative; /* ensure tooltips appear above content */
    z-index: 2;
  }

  .brand {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    min-width: 0; /* allow children to truncate */
  }

  .title {
    font-size: 1.125rem;
    font-weight: 600;
    line-height: 1.25;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
  }

  .button-group {
    margin-left: auto;
    display: flex;
    gap: 0.5rem;
  }

  /* Close button only shown on small / fullscreen views */
  .close-mobile {
    display: none !important; /* default hidden on large screens */
  }

  /* Ensure regular controls are visible on large screens */
  .button-group .icon-button {
    display: inline-flex;
  }

  @media screen and (max-width: 640px) {
    .close-mobile {
      display: inline-flex !important;
    }

    /* hide regular maximize/minimize controls on mobile to reduce clutter (keep reset visible) */
    .button-group .icon-button:not(.close-mobile):not(.reset-mobile) {
      display: none !important;
    }

    /* Show reset on mobile and put it left of the close button */
    .reset-mobile {
      display: inline-flex !important;
      order: 0;
      margin-right: 0.5rem;
    }
    .close-mobile {
      order: 1;
    }
  }

  .icon-button {
    background: transparent;
    border: none;
    color: white;
    cursor: pointer;
    position: relative; /* enable tooltip positioning */
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  /* Custom tooltip using aria-label as content */
  .icon-button::after {
    content: attr(aria-label);
    position: absolute;
    top: calc(
      100% + 8px
    ); /* place tooltip below to avoid clipping by container */
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(17, 24, 39, 0.95); /* near-black */
    color: #fff;
    padding: 6px 8px; /* padding as requested */
    border-radius: 6px; /* rounded corners as requested */
    font-size: 12px;
    line-height: 1;
    white-space: nowrap;
    opacity: 0;
    pointer-events: none;
    transition:
      opacity 150ms ease,
      transform 150ms ease;
    z-index: 1000;
  }

  .icon-button:hover::after,
  .icon-button:focus-visible::after {
    opacity: 1;
    transform: translateX(-50%) translateY(2px); /* slight downward nudge */
  }

  /* Keep the right-most tooltip inside the container by aligning it to the right edge of the button */
  .button-group .icon-button:last-child::after {
    left: auto;
    right: 0;
    transform: translateY(2px);
  }

  .content {
    flex: 1;
    overflow: hidden; /* keep scrolling content masked while tooltips can overflow from header */
  }
</style>
