import { writable, derived, get } from 'svelte/store';
import type { Message } from '../types';
import { chatApi } from '../utils/api';

export interface ChatStore {
    messages: Message[];
    isLoading: boolean;
    currentChatId: string | null;
    error: string | null;
    hasMore: boolean;
    currentPage: number;
}

const initialState: ChatStore = {
    messages: [],
    isLoading: false,
    currentChatId: null,
    error: null,
    hasMore: true,
    currentPage: 1
};

export const chatStore = writable<ChatStore>(initialState);

// Derived stores for computed values
export const formattedMessages = derived(chatStore, ($store) =>
    $store.messages.map(msg => ({
        ...msg,
        message: msg.message
    }))
);

export const sendMessage = async (content: string, settings: { bot_id: string }) => {
    const userId = localStorage.getItem('userId');
    const botId = settings.bot_id;

    if (!userId || !botId) {
        throw new Error('User ID and Bot ID are required');
    }

    // Snapshot store BEFORE we add the streaming placeholder to detect "first send"
    const prevState = get(chatStore);

    let accumulatedText = '';

    // Add user message
    chatStore.update(state => ({
        ...state,
        messages: [
            ...state.messages,
            {
                id: Date.now().toString(),
                chatId: state.currentChatId || '',
                role: 'human',
                message: content,
                created_at: new Date().toISOString(),
            },
            {
                id: 'streaming',
                chatId: state.currentChatId || '',
                role: 'ai',
                message: '',
                created_at: new Date().toISOString(),
            },
        ],
        isLoading: true,
    }));

    try {
        // Only include conversation_id if we already have messages in the current session
        // This avoids sending stale IDs (e.g., from localStorage) on the first message
        const includeConversation = !!prevState.currentChatId && prevState.messages.length > 0;
        const body: any = {
            bot_id: botId,
            prompt: content,
            stream: true,
            source: "widget"
        };
        if (includeConversation) {
            body.conversation_id = prevState.currentChatId;
        }

        // Use fetch streaming to properly handle SSE over POST
        const url = `${import.meta.env.VITE_FLUENT_AI_URL}/chat/completion`;
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'text/event-stream',
                'Cache-Control': 'no-cache'
            },
            body: JSON.stringify(body)
        });

        if (!response.ok || !response.body) {
            throw new Error(`Request failed: ${response.status} ${response.statusText}`);
        }

        const reader = response.body.getReader();
        const decoder = new TextDecoder();
        let buffer = '';
        let currentEvent = '';
        let dataLines: string[] = [];

        const flushEvent = () => {
            // If there's no current event but we still have buffered data, ignore.
            if (!currentEvent && dataLines.length === 0) return;
            const payload = dataLines.join('\n');
            // Default unnamed events to 'message' to support data-only SSE
            const eventName = currentEvent || 'message';
            // Incrementally update on 'message' events. Finalization will happen
            // only once at end-of-stream (see below), since no sentinel or 'end' event.
            if (eventName === 'message') {
                if (payload && payload.length > 0) {
                    accumulatedText += payload;
                    // Update the streaming placeholder with accumulated text (keep placeholder id)
                    chatStore.update(s => ({
                        ...s,
                        messages: s.messages.map(m => m.id === 'streaming' ? { ...m, message: accumulatedText } : m)
                    }));
                }
            } else if (eventName === 'sources') {
                // Attempt to parse JSON sources array and attach to streaming message.
                // Rendering of sources is controlled by the UI (e.g., Message.svelte's showSources prop).
                try {
                    const parsed = JSON.parse(payload);
                    if (Array.isArray(parsed)) {
                        chatStore.update(s => ({
                            ...s,
                            messages: s.messages.map(m => m.id === 'streaming' ? { ...m, sources: parsed } : m)
                        }));
                    }
                } catch {
                    // ignore malformed sources payload
                }
            } else if (eventName === 'conversation_id') {
                const chatId = payload.trim();
                localStorage.setItem('currentChatId', chatId);
                chatStore.update(s => ({
                    ...s,
                    currentChatId: chatId,
                    messages: s.messages.map(m => !m.chatId ? { ...m, chatId } : m)
                }));
            } else if (eventName === 'token_usage') {
                // no-op for now
            }
            // reset for next event
            currentEvent = '';
            dataLines = [];
        };

        while (true) {
            const { value, done } = await reader.read();
            if (done) break;
            buffer += decoder.decode(value, { stream: true });

            // Process complete lines
            const lines = buffer.split(/\r?\n/);
            // Save last partial line back to buffer
            buffer = lines.pop() ?? '';

            for (const line of lines) {
                if (line.startsWith('event:')) {
                    // If we were in the middle of an event, flush it before switching
                    flushEvent();
                    currentEvent = line.slice(6).trim();
                } else if (line.startsWith('data:')) {
                    dataLines.push(line.slice(5));
                } else if (line.trim() === '') {
                    // Blank line signals event dispatch
                    flushEvent();
                }
            }
        }

        // Flush any trailing data
        if (buffer.length > 0) {
            const trailing = buffer.trimEnd();
            if (trailing) {
                const maybeLines = trailing.split(/\r?\n/);
                for (const line of maybeLines) {
                    if (line.startsWith('event:')) {
                        // flush previous event then start a new one
                        flushEvent();
                        currentEvent = line.slice(6).trim();
                    } else if (line.startsWith('data:')) {
                        dataLines.push(line.slice(5));
                    }
                }
            }
        }
        // Final flush at end of stream: process any trailing payload one last time
        flushEvent();
        // Now finalize the streaming message by replacing the placeholder and
        // clearing loading state
        chatStore.update(s => {
            const streamingMsg = s.messages.find(m => m.id === 'streaming');
            if (!streamingMsg) {
                return { ...s, isLoading: false };
            }
            const finalMessage: Message = {
                id: Date.now().toString(),
                chatId: s.currentChatId || '',
                role: 'ai',
                message: accumulatedText,
                created_at: new Date().toISOString(),
                // carry over any sources captured during streaming
                sources: (streamingMsg as any).sources || undefined,
            };
            return {
                ...s,
                messages: s.messages.map(m => m.id === 'streaming' ? finalMessage : m),
                isLoading: false
            };
        });
    } catch (error) {
        console.error('Error sending message:', error);
        chatStore.update(s => ({
            ...s,
            isLoading: false
        }));
        throw error;
    }
};

export const clearMessages = () => {
    localStorage.removeItem('currentChatId');
    chatStore.update(state => ({
        ...state,
        messages: [],
        currentChatId: null,
        hasMore: true,
        currentPage: 1
    }));
};

export const resetFilters = () => {
    chatStore.update(state => ({
        ...state,
        messages: [],
        currentChatId: null,
    }));
};

export const loadChatHistory = async (chatId: string, page: number = 1, limit: number = 10) => {
    try {
        const response = await chatApi.get(`/chat/${chatId}/messages`, {
            params: {
                page,
                page_size: limit,
            }
        });

        const rawMessages = (response.data?.messages || []) as any[];
        // Normalize and ensure messages are chronological (oldest -> newest)
        const mapped = (rawMessages.map((m, idx) => ({
            id: (m.id ?? `${Date.now()}-${idx}`).toString(),
            chatId: chatId,
            role: (m.role ?? m.type) === 'human' ? 'human' : 'ai',
            message: m.message ?? m.content ?? '',
            created_at: m.created_at ?? new Date().toISOString(),
            // pass through sources if backend returned them
            sources: Array.isArray(m.sources) ? m.sources : undefined,
        })) as Message[]).sort((a, b) => {
            const ta = new Date(a.created_at).getTime();
            const tb = new Date(b.created_at).getTime();
            return ta - tb;
        });

        const total = response.data?.total ?? (response.data?.pagination?.total ?? 0);
        const hasNext = response.data?.has_next ?? (total ? page * limit < total : false);

        chatStore.update(state => ({
            ...state,
            messages: page === 1 ? mapped : [...mapped, ...state.messages],
            hasMore: !!hasNext,
            currentPage: page,
            currentChatId: chatId
        }));

        localStorage.setItem('currentChatId', chatId);
    } catch (error) {
        console.error('Error loading chat history:', error);
        chatStore.update(state => ({
            ...state,
            error: 'Failed to load chat history'
        }));
    }
};
